.AddPost {
  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 30px;
  }

  &__textarea,
  &__input {
    width: 300px;
    display: block;
    box-sizing: border-box;
    margin-bottom: 15px;
  }

  &__button {
    background-color: lightgray;
  }
}